import { Component, OnInit } from '@angular/core';
import { DropshipService } from '../../../../auth/_services/dropship.service';
import { CategoryService } from '../../../../auth/_services/category.service';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from "@angular/router";
import { SelectModule } from 'ng2-select';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';

@Component({
    selector: 'app-dropship',
    templateUrl: "dropship.component.html",
    //styles: ["style.component.css"]

})
export class DropshipComponent implements OnInit {


    constructor(private imgBaseUrl: ScriptLoaderService, private router: Router, private toastr: ToastrService, private _categoryService: CategoryService, private _dropshipService: DropshipService) { }
    public dropshipData;
    public categoryData;
    public searchValue = "";
    public dropDownValue = '';
    public brandData = '';
    p: number = 1;
    public productData = '';
    public checkAllList = '';
    selectedPro: any = [];
    inputData: any = [];
    collectionName: any = '';
    public ImgUrl: any;

    /*public items:Array<string> = ['Amsterdam', 'Antwerp', 'Athens', 'Barcelona',
    'Berlin', 'Birmingham', 'Bradford', 'Bremen', 'Brussels', 'Bucharest',
    'Budapest', 'Cologne', 'Copenhagen', 'Dortmund', 'Dresden', 'Dublin', 'Düsseldorf',
    'Essen', 'Frankfurt', 'Genoa', 'Glasgow', 'Gothenburg', 'Hamburg', 'Hannover',
    'Helsinki', 'Leeds', 'Leipzig', 'Lisbon', 'Łódź', 'London', 'Kraków', 'Madrid',
    'Málaga', 'Manchester', 'Marseille', 'Milan', 'Munich', 'Naples', 'Palermo',
    'Paris', 'Poznań', 'Prague', 'Riga', 'Rome', 'Rotterdam', 'Seville', 'Sheffield',
    'Sofia', 'Stockholm', 'Stuttgart', 'The Hague', 'Turin', 'Valencia', 'Vienna',
    'Vilnius', 'Warsaw', 'Wrocław', 'Zagreb', 'Zaragoza'];*/
    public items: Array<string> = ['Amsterdam', 'Antwerp'];

    private value: any = ['Athens'];
    private _disabledV: string = '0';
    private disabled: boolean = false;

    private get disabledV(): string {
        return this._disabledV;
    }

    private set disabledV(value: string) {
        this._disabledV = value;
        this.disabled = this._disabledV === '1';
    }

    public selected(value: any): string {
        value.id = this.items.indexOf(value.text);
        return value;
        /*console.log('Selected value is: ', value);*/

    }

    public removed(value: any): void {
        console.log('Removed value is: ', value);
    }

    public refreshValue(value: any): void {
        this.value = value;
    }

    public itemsToString(value: Array<any> = []): string {

        return value
            .map((item: any) => {
                return item.text;
            }).join(',');
    }


    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.dropship_add != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        // localStorage.setItem('section', 'dropship');
        this.ImgUrl = this.imgBaseUrl._imgBaseUrl;

        this._dropshipService.getAll().subscribe(
            data => {
                // console.log(data);
                this.dropshipData = data.success;
            }
        );

        this._categoryService.getAll().subscribe(
            data => {
                this.categoryData = data.success;
                //this.items = data.success;
                let num = [];
                this.categoryData.forEach(function(category) {
                    num[category.id] = category.category_name;
                });
                this.items = num;
                //this.items.push()
                console.log('this.items');
                console.log(this.items);
            }
        );





        // console.log("hello");
    }

    public search() {
        this._dropshipService.searching({ search_value: this.searchValue, dropdown_value: this.dropDownValue }).subscribe(
            data => {
                console.log(data);
                this.dropshipData = data.success;
            }
        );
    }

    public dropship_delete(del_id) {

        console.log(del_id);

        let value = confirm('Are you want to delete this dropship ?');
        if (value) {

            this._dropshipService.del_dropship(del_id).subscribe(
                data => {
                    this.dropshipData = data.data;
                }
            );
        }

    }


    public changeStatus(dropship: any) {

        if (dropship.dropship_status == 'Active') {
            dropship.dropship_status = 'Inactive';

            this._dropshipService.updateStatus(dropship.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        } else {
            dropship.dropship_status = 'Active';
            this._dropshipService.updateStatus(dropship.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        }
    }

    public orderFilter() {
        let loggedUserId = JSON.parse(localStorage.getItem('currentUser'));

        this._dropshipService.filter({ search_value: this.searchValue, dropdown_value: this.dropDownValue, loggedUserId: loggedUserId.success.id, loggedUserRole: loggedUserId.success.role }).subscribe(
            data => {
                this.productData = data.success;
            }
        );
    }

    public checkAll(productData) {
        let loggedUserId = JSON.parse(localStorage.getItem('currentUser'));
    }

    public isAnswerProvided(event, product: any) {

        if (this.selectedPro.indexOf(product.product_id) == -1) {
            this.selectedPro.push(product.product_id);
        } else {
            this.selectedPro.splice(this.selectedPro.indexOf(product.product_id), 1);
        }
    }

    public addToCollection(selectedPro: any) {

        let loggedUserId = JSON.parse(localStorage.getItem('currentUser'));

        this.inputData = { 'products_data': selectedPro, 'user_id': loggedUserId.success.id, 'collection_name': this.collectionName };
        console.log('inputData');
        console.log(this.inputData);
        this._dropshipService.createCollection(this.inputData).subscribe(
            data => {
                this.toastr.success('Product Added Successfully', 'Success Message');
            },
            error => {

                this.toastr.error('Please Enter Valid data', 'Error Message');

            }
        );

    }



}
